import Vuex from 'vuex'
import Vue from 'vue'
import mblog from '../data/blogs'
import mfmovie from '../data/movie'

Vue.use(Vuex);

const actions = {
    // Archive 首次挂载时获取 blogs 和 blogsCategories
    get_blogs(content) {
        content.state.blogs = mblog.blogs.sort((a, b) => b['data'] - a['data'])

        const ori_categories = content.state.blogs.map(b => b.categories)
        content.state.blogsCategories = [...new Set(ori_categories)].map(m => {
            return {
                category: m,
                isActived: true
            }
        })
    },
    get_blog_display_now(content, [mdTitle, routePath]) {
        content.state.blogDisplayNow = routePath
        content.state.blogDisplayNowTitle = mdTitle
    },
    get_movie(content) {
        content.state.movie = mfmovie.movie
    }
}

const mutations= {

}

const state = {
    blogs: [],
    // 保存选择展示的分类，Archive首次挂载时，isActioved 默认为 true
    blogsCategories: [],
    blogDisplayNowTitle: '',
    blogDisplayNow: '',
    movie: [],
}

const getters = {
    // 根据 state.blogsCategories 判断展示的内容
    blogsDisplay(state) {
        const categoriesSelected = state.blogsCategories.map(m => {
            if(m.isActived) {
                return m.category
            }
        }).filter(res => res!=undefined)
        
        return state.blogs.map(m => {
            if(categoriesSelected.includes(m.categories)) {
                return m
            }
        }).filter(res => res!=undefined)
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})